body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media all {
:root {
  --ravnBlack: #121212;
  --TextDark: #333333;
  --TextLight: #828282;
  --TextEmphasis: #EC5757;
}

}
.headerRAVN {
  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;
  display: flex;
  align-items: center;
  letter-spacing: 0.0125em;

  padding-left: 33px;
  padding-top: 16px;
  padding-bottom: 16px;

  color: white;
  background: var(--ravnBlack);
  width: 100%;

}

.Loading {

  color: var(--TextLight);

  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;

  display: flex;
  align-items: center;
  letter-spacing: 0.0125em;

  padding-top: 16px;
  padding-bottom: 16px;

  margin: 0px;
  height: 20px;
  width: 350px;

  justify-content: center
}

.loadtext {

  height: 20px;
  padding-left: 8px;
}

.failed{
  color: var(--TextEmphasis);

  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;
  height: 20px;

  display: flex;
  align-items: center;
  letter-spacing: 0.0125em;

  padding-top: 16px;
  padding-bottom: 16px;
  width: 350px;
  justify-content: center;
}


.listPeople{
  float:left;
  width: 350px;
  margin: 0px;
  padding: 0px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);

}

.bottonbar{
  float:left;
  width: 100%;
}

.properties{
    position: absolute;
    display : grid;
    width: calc(100% - 350px);


    letter-spacing: 0.0125em;

    margin-left: 450px;



    color: var(--TextDark);

}

.tituloPro{

  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;

  padding-left: 33px;
  padding-top: 32px;
  padding-bottom: 8px;
  justify-content: left;


}

.character{


  font-family: system-ui;
  font-style: normal;
  -webkit-column-rule-style: solid;
          column-rule-style: solid;
  margin: 0px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin-right: 15%;
}

.datapro{
  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 17px;
  height: 17px;
  float:left;

  padding-left: 33px;
  padding-right:  100px;
  padding-top: 16px;
  padding-bottom: 16px;
  color: var(--TextLight);
}

.dataproV{
  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 17px;
  height: 17px;
  float: left;
  right: 33px;
  padding-right: 33px;
  padding-top: 16px;
  padding-bottom: 16px;

  color: var(--TextDark);
}

.person{
  width: auto;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 16px;
  transform: translateX(0);
}

.name{
  width: auto ;
  color: var(--TextDark);
  font-family: system-ui;
  font-style: normal;
  font-size: 17px;
  line-height: 20px;
  display: flex;
  align-items: left;
  letter-spacing: 0.0125em;
}

.specie{
  width: auto ;
  color: var(--TextLight);
  font-family: system-ui;
  font-style: normal;

  font-size: 14px;
  height: 17px;
  display: flex;
  align-items: left;
  letter-spacing: 0.0125em;

}

.arrow{
  position: absolute;
  width: 24px;
  height: 24px;
  right: 16px;
  top: calc(50% - 24px/2 + 1.5px);

}

